import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import ThumbnailChooser from 'components/ThumbnailChooser';
import AddCaptionButton from 'components/AddCaptionButton';
import RemoveSelectedImageButton from 'components/RemoveSelectedImageButton';
import ImageOptionsDialog from 'components/ImageOptionsDialog';
import MemoryPhotoAlbum from 'components/MemoryPhotoAlbum';
import AddPhotos from 'components/AddPhotos';

export default function MemoryImages({
  editable,
  images,
  handlePhotoClick,
  handleThumbnailUpdate,
  deleteImage,
  addImage,
  memThumbnail,
  updateCaption,
}) {
  const [edit, setEdit] = useState(editable);
  const [thumbnail, setThumbnail] = useState(memThumbnail);
  const [editingIndex, setEditingIndex] = useState(null);
  const [editOpen, setEditOpen] = useState(false);

  useEffect(() => setEdit(editable), [editable]);
  useEffect(() => setThumbnail(memThumbnail), [memThumbnail]);

  const handleThumbnailChange = (ind) => {
    setThumbnail(ind);
    handleThumbnailUpdate(ind);
  };

  const handleImageDelete = (ind) => deleteImage(ind);
  const handleCaptionChange = (index, caption) => {
    setEditOpen(false);
    updateCaption(index, caption);
  };

  console.log(thumbnail, handleThumbnailChange, handleImageDelete);

  const renderPhotos = ({
    layout,
    imageProps: {
      alt, style, title, ...restImageProps
    },
  }) => (
    <Button component="div" sx={{ padding: '2px' }}>
      <img
        alt={alt}
          // eslint-disable-next-line react/jsx-props-no-spreading
        style={{ ...style, width: layout.width, padding: 0 }}
          // eslint-disable-next-line react/jsx-props-no-spreading
        {...restImageProps}
      />
      {
        layout.index !== thumbnail ? (
          <RemoveSelectedImageButton index={layout.index} handleRemoveImage={handleImageDelete} />
        ) : null
      }
      <ThumbnailChooser
        index={layout.index}
        thumbnail={thumbnail}
        handleThumbnailChange={handleThumbnailChange}
      />
      <AddCaptionButton
        handleClick={() => {
          setEditingIndex(layout.index);
          setEditOpen(true);
        }}
        caption={('caption' in images[layout.index]) ? images[layout.index].caption : ''}
      />
    </Button>
  );

  const OptionsRow = (
    <Stack direction="row" spacing={2} justifyContent="space-between" alignItems="center">
      <Typography variant="h6" color="muted.main">
        Images:
        {' '}
        {images.length}
      </Typography>
      <AddPhotos handleChange={addImage} />
    </Stack>
  );

  return edit ? (
    <Stack direction="column" spacing={2} sx={{ p: 2 }}>
      {OptionsRow}
      <MemoryPhotoAlbum images={images} renderPhoto={renderPhotos} />
      {(editingIndex !== null)
        ? (
          <ImageOptionsDialog
            imageCaption={('caption' in images[editingIndex]) ? images[editingIndex].caption : ''}
            index={editingIndex}
            src={images[editingIndex].src}
            open={editOpen}
            handleClose={() => { setEditOpen(false); setEditingIndex(null); }}
            handleSubmit={handleCaptionChange}
          />
        ) : null}
    </Stack>
  ) : (
    <MemoryPhotoAlbum images={images} handlePhotoClick={handlePhotoClick} />
  );
}

MemoryImages.propTypes = {
  editable: PropTypes.bool.isRequired,
  memThumbnail: PropTypes.number.isRequired,
  images: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
      title: PropTypes.string,
      caption: PropTypes.string,
    }),
  ).isRequired,
  handlePhotoClick: PropTypes.func.isRequired,
  handleThumbnailUpdate: PropTypes.func.isRequired,
  deleteImage: PropTypes.func.isRequired,
  addImage: PropTypes.func.isRequired,
  updateCaption: PropTypes.func.isRequired,
};
