import PropTypes from 'prop-types';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import Button from '@mui/material/Button';

import SelectAllCheckboxCell from './SelectAllCheckboxCell';

export default function TableHeader({
  numSelected, rowCount, onSelectAllClick, handleUnshareClick, unsharing,
}) {
  return (
    <TableHead>
      <TableRow>
        <SelectAllCheckboxCell
          numSelected={numSelected}
          rowCount={rowCount}
          onSelectAllClick={onSelectAllClick}
          key="selectAll"
        />
        <TableCell
          align="center"
          sx={{ fontWeight: 'bold', fontSize: '1em' }}
          key="message"
        >
          {numSelected === 0 ? 'Shared With' : `${numSelected} selected`}
        </TableCell>
        <TableCell align="center" sx={{ fontWeight: 'bold' }} key="action">
          <Button
            variant="contained"
            color="error"
            sx={{ textTransform: 'none' }}
            disabled={(numSelected === 0) || (unsharing === true)}
            onClick={handleUnshareClick}
          >
            Unshare
          </Button>
        </TableCell>
      </TableRow>
    </TableHead>
  );
}

TableHeader.propTypes = {
  numSelected: PropTypes.number.isRequired,
  rowCount: PropTypes.number.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  handleUnshareClick: PropTypes.func.isRequired,
  unsharing: PropTypes.bool.isRequired,
};
