import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TablePagination from '@mui/material/TablePagination';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

import ShareeRow from './ShareRow';
import TableHeader from './TableHeader';

export default function ExistingShares({ shareData, onUnShare, unsharing }) {
  const rowsPerPage = 5;
  const [page, setPage] = useState(0);
  const [numSelected, setNumSelected] = useState(0);
  const [selected, setSelected] = useState(Array(shareData.length).fill(0));

  useEffect(() => {
    setSelected(Array(shareData.length).fill(0));
    setNumSelected(0);
  }, [shareData]);

  const handleCheckBoxClick = (ind) => {
    if (selected[ind] === 1) {
      setNumSelected((prevSelected) => prevSelected - 1);
    } else {
      setNumSelected((prevSelected) => prevSelected + 1);
    }

    setSelected([
      ...selected.slice(0, ind),
      1 - selected[ind],
      ...selected.slice(ind + 1),
    ]);
  };

  const handleSelectAllClick = () => {
    if (selected.length > 0 && numSelected < selected.length) {
      setSelected(Array(shareData.length).fill(1));
      setNumSelected(selected.length);
    } else {
      setSelected(Array(shareData.length).fill(0));
      setNumSelected(0);
    }
  };

  const handleUnshare = () => {
    const unshareEmails = [];
    for (let i = 0; i < selected.length; i += 1) {
      if (selected[i] === 1) {
        unshareEmails.push(shareData[i].email);
      }
    }
    onUnShare(unshareEmails);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  return (shareData.length > 0) ? (
    <>
      {
        unsharing === true ? (
          <Typography color="primary" variant="h6" sx={{ p: 2 }}>UnSharing Memory. Please wait...</Typography>
        ) : null
      }
      <TableContainer component={Paper}>
        <Table stickyHeader aria-label="memory share table">
          <TableHeader
            numSelected={numSelected}
            rowCount={shareData.length}
            onSelectAllClick={handleSelectAllClick}
            handleUnshareClick={handleUnshare}
            unsharing={unsharing}
          />
          <TableBody>
            {shareData
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((sharee, index) => (
                <ShareeRow
                  key={sharee.email}
                  email={sharee.email}
                  name={sharee.name}
                  picture={sharee.picture}
                  invitation={sharee.status.invitation}
                  accept={sharee.status.accept}
                  isSelected={selected[page * rowsPerPage + index] === 1}
                  onCheckBoxClick={() => handleCheckBoxClick(page * rowsPerPage + index)}
                />
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[rowsPerPage]}
        component="div"
        count={selected.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
      />
    </>

  ) : null;
}

ExistingShares.propTypes = {
  shareData: PropTypes.arrayOf(PropTypes.shape(
    {
      email: PropTypes.string.isRequired,
      status: PropTypes.shape({
        accept: PropTypes.string.isRequired,
        invitation: PropTypes.string.isRequired,
      }).isRequired,
      name: PropTypes.string,
      picture: PropTypes.string,
    },
  )).isRequired,
  onUnShare: PropTypes.func.isRequired,
  unsharing: PropTypes.bool.isRequired,
};
