import ActionIcon from 'components/ActionIcon';
import ShareIcon from '@mui/icons-material/Share';
import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import Typography from '@mui/material/Typography';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import Divider from '@mui/material/Divider';
import useEmail from 'hooks/email';

import NewShares from 'components/NewShares';
import ExistingShares from 'components/ExistingShares';
import CloseButton from 'components/CloseButton';
import api from '../Api';

export function ShareMemoryIcon() {
  return <ActionIcon icon={<ShareIcon />} label="Share" />;
}

export function ShareComponent({
  confirm, memoryShareCallback, memoryUnShareCallback, closeCallback, memoryID,
}) {
  const shareTitle = 'Share Memory';
  const [
    items,
    value,
    error,
    setItems,
    handleKeyDown,
    handleChange,
    handleDelete,
    handlePaste,
  ] = useEmail();

  const [shareData, setShareData] = useState(null);
  const [sharing, setSharing] = useState(false);
  const [unsharing, setUnSharing] = useState(false);

  useEffect(async () => {
    try {
      const data = await api.getShareData(memoryID);
      setShareData(data);
    } catch (err) {
      /* eslint-disable no-console */
      console.log(err);
    }
  }, []);

  const [open, setOpen] = useState(confirm);
  useEffect(() => setOpen(confirm), [confirm]);

  const handleClose = () => {
    setOpen(false);
    closeCallback();
  };

  const handleSubmit = async () => {
    //  callback(items);
    try {
      setSharing(true);
      const shareResults = await api.shareMemory(memoryID, items);
      memoryShareCallback(shareResults);
      const resultsJSON = await shareResults.json();
      setItems([]);
      setShareData(resultsJSON);
      setSharing(false);
    } catch (err) {
      /* eslint-disable no-console */
      console.log(err);
      setSharing(false);
    }
  };

  const handleUnshare = async (emails) => {
    //  callback(items);
    try {
      setUnSharing(true);
      const shareResults = await api.revokeShare(memoryID, emails);
      console.log(shareResults);
      memoryUnShareCallback(shareResults);
      const resultsJSON = await shareResults.json();
      setShareData(resultsJSON);
      setUnSharing(false);
    } catch (err) {
      /* eslint-disable no-console */
      console.log(err);
      setUnSharing(false);
    }
  };

  const existingShares = (shareData !== null) ? (
    <>
      <Typography sx={{ m: 2, fontSize: '1.2rem' }}>Existing Shares</Typography>
      <ExistingShares shareData={shareData} onUnShare={handleUnshare} unsharing={unsharing} />
    </>
  ) : null;

  return (
    <Dialog
      open={open}
      sx={{
        '& .MuiPaper-root': { m: 0 },
        backgroundColor: 'faded.black',
      }}
    >
      <DialogTitle>
        {shareTitle}
        <CloseButton handleClose={handleClose} />
      </DialogTitle>
      <Divider />
      <Typography sx={{ m: 2 }}>
        New Shares (
        {items.length}
        {' '}
        added)
      </Typography>
      <NewShares
        items={items}
        value={value}
        error={error}
        handleKeyDown={handleKeyDown}
        handleChange={handleChange}
        handleDelete={handleDelete}
        handlePaste={handlePaste}
        handleClear={() => setItems([])}
        handleSubmit={handleSubmit}
        sharing={sharing}
      />
      <Divider />
      {existingShares}
    </Dialog>
  );
}

ShareComponent.propTypes = {
  confirm: PropTypes.bool.isRequired,
  memoryShareCallback: PropTypes.func.isRequired,
  memoryUnShareCallback: PropTypes.func.isRequired,
  closeCallback: PropTypes.func.isRequired,
  memoryID: PropTypes.string.isRequired,
};
