import PropTypes from 'prop-types';
import ActionIcon from 'components/ActionIcon';
import Confirm from 'components/confirmation';
import DeleteIcon from '@mui/icons-material/Delete';
import api from '../Api';

export function DeleteMemoryIcon() {
  return (
    <ActionIcon icon={<DeleteIcon />} label="Delete" />
  );
}

export function ConfirmDelete({ callback, open, closeCallback }) {
  const DeleteTitle = 'Delete Memory';
  const DeleteConfirmMessage = 'Do you want to delete this memory?';
  return (
    <Confirm
      confirm={open}
      title={DeleteTitle}
      handleCallback={callback}
      handleCloseCallback={() => closeCallback()}
      message={DeleteConfirmMessage}
    />
  );
}

export default async function deleteMemory(bucket, authURL, memoryID, memoryDeleteCallback) {
  try {
    const deleteResults = await api.deleteMemory(bucket, authURL, memoryID);
    memoryDeleteCallback(deleteResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

ConfirmDelete.propTypes = {
  open: PropTypes.bool.isRequired,
  callback: PropTypes.func.isRequired,
  closeCallback: PropTypes.func.isRequired,
};
