import PropTypes from 'prop-types';
import AddPhotoIcon from '@mui/icons-material/AddPhotoAlternate';
import OptionsButton from 'components/OptionsButton';

export default function AddPhotos({ handleChange }) {
  return (
    <OptionsButton name="add">
      <input type="file" hidden multiple accept="image/*" onChange={handleChange} />
      <AddPhotoIcon sx={{ fontSize: '2rem' }} />
      Add Images
    </OptionsButton>
  );
}

AddPhotos.propTypes = {
  handleChange: PropTypes.func.isRequired,
};
