import PropTypes from 'prop-types';
import { useState } from 'react';
import Collapse from '@mui/material/Collapse';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';

import StatusTable from './StatusTable';
import CheckboxCell from './CheckboxCell';
import PersonalDetailsCell from './PersonalDetailsCell';
import ExpandStatusIconCell from './ExpandStatusIconCell';

export default function ShareeRow({
  name,
  email,
  picture,
  invitation,
  accept,
  isSelected,
  onCheckBoxClick,
}) {
  const [open, setOpen] = useState(false);
  const handleClick = () => setOpen(!open);

  return (
    <>
      <TableRow
        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
        key={`${{ email }}details`}
      >
        <CheckboxCell selected={isSelected} handleChange={onCheckBoxClick} />
        <PersonalDetailsCell email={email} picture={picture} name={name} />
        <ExpandStatusIconCell open={open} handleClick={handleClick} />
      </TableRow>
      <TableRow key={`${{ email }}status`}>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography gutterBottom component="div">
                Email:
                {' '}
                {email}
              </Typography>
              <Typography variant="h6" gutterBottom component="div">
                Status
              </Typography>
              <StatusTable invitation={invitation} accept={accept} />
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  );
}

ShareeRow.propTypes = {
  name: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  picture: PropTypes.string.isRequired,
  invitation: PropTypes.string.isRequired,
  accept: PropTypes.string.isRequired,
  isSelected: PropTypes.bool.isRequired,
  onCheckBoxClick: PropTypes.func.isRequired,
};
