import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import EmailInput from 'components/EmailInput';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

export default function NewShares({
  items,
  value,
  error,
  handleKeyDown,
  handleChange,
  handleDelete,
  handlePaste,
  handleClear,
  handleSubmit,
  sharing,
}) {
  return (
    <Box sx={{ p: 2 }}>
      <EmailInput
        items={items}
        value={value}
        error={error}
        handleKeyDown={handleKeyDown}
        handleChange={handleChange}
        handleDelete={handleDelete}
        handlePaste={handlePaste}
      />
      {
        sharing === true ? (
          <Typography color="primary" variant="h6" sx={{ pb: 2 }}>
            Sharing Memory. Please wait...
          </Typography>
        ) : null
      }
      <Stack spacing={2} direction="row">
        <Button variant="outlined" onClick={handleClear}>
          Clear
        </Button>
        <Button
          variant="contained"
          onClick={handleSubmit}
          disabled={items.length === 0 || sharing === true}
        >
          Share
        </Button>
      </Stack>
    </Box>
  );
}

NewShares.propTypes = {
  items: PropTypes.arrayOf(PropTypes.string).isRequired,
  value: PropTypes.string.isRequired,
  error: PropTypes.string.isRequired,
  handleKeyDown: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  handleDelete: PropTypes.func.isRequired,
  handlePaste: PropTypes.func.isRequired,
  handleClear: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  sharing: PropTypes.bool.isRequired,
};
