import PropTypes from 'prop-types';
import PhotoAlbum from 'react-photo-album';
import { useState, useEffect } from 'react';
import utils from 'utils';

export default function MemoryPhotoAlbum({ images, handlePhotoClick, renderPhoto }) {
  const [arrangement, setArrangement] = useState(null);
  useEffect(() => utils.handleArrangement(images, setArrangement), [images]);

  return renderPhoto ? (
    <PhotoAlbum
      layout={arrangement}
      photos={images}
      onClick={handlePhotoClick}
      spacing={1}
      renderPhoto={renderPhoto}
      rowConstraints={utils.handleMaxPhotos}
      columns={utils.handleColumns}
    />
  ) : (
    <PhotoAlbum
      layout={arrangement}
      photos={images}
      onClick={handlePhotoClick}
      spacing={1}
      rowConstraints={utils.handleMaxPhotos}
      columns={utils.handleColumns}
    />
  );
}

MemoryPhotoAlbum.defaultProps = {
  renderPhoto: null,
  handlePhotoClick: () => {},
};

MemoryPhotoAlbum.propTypes = {
  images: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
  handlePhotoClick: PropTypes.func,
  renderPhoto: PropTypes.func,
};
