import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Box from '@mui/material/Box';
import { alpha } from '@mui/material/styles';
import * as yup from 'yup';
import utils from 'utils';

export default function ImageOptionsDialog({
  index, imageCaption, src, open, handleClose, handleSubmit,
}) {
  console.log(index);
  const [caption, setCaption] = useState(imageCaption);
  const [captionError, setCaptionError] = useState('');

  useEffect(() => setCaption(imageCaption), [imageCaption]);

  const onCaptionChange = (event) => {
    setCaption(event.target.value);
  };

  const captionSchema = yup.object({
    caption: yup.string().max(40, 'Maximum length of image caption is 40'),
  }).required();

  useEffect(() => {
    if (caption.length > 0) {
      utils.isFieldValid('caption', caption, setCaptionError, captionSchema);
    }
  }, [caption]);

  const isValid = async () => {
    const captionValid = await utils.isFieldValid('caption', caption, setCaptionError, captionSchema);
    return captionValid;
  };

  const submitForm = async () => {
    const valid = await isValid();
    if (valid) {
      handleSubmit(index, caption);
      handleClose();
    }
  };

  return (
    <Dialog open={open} sx={{ border: '2px solid white', backgroundColor: alpha('#000', 0.8) }}>
      <DialogTitle>
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
        Add Image Caption
      </DialogTitle>
      <DialogContent dividers>
        <Box
          component="img"
          alt="test"
          src={src}
          width="100%"
          sx={{ pb: 2, borderRadius: 3, height: '50vh' }}
        />
        <TextField
          variant="outlined"
          label="Image Caption"
          defaultValue={caption}
          onChange={onCaptionChange}
          sx={{ width: '100%' }}
          helperText={captionError || 'E.g. Birthday Cake Cutting'}
          error={!!captionError}
        />
      </DialogContent>
      <DialogActions>
        <Button variant="outlined" onClick={submitForm}>OK</Button>
        <Button variant="text" onClick={handleClose}>Cancel</Button>
      </DialogActions>
    </Dialog>
  );
}

ImageOptionsDialog.propTypes = {
  index: PropTypes.number.isRequired,
  imageCaption: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
};
