import PropTypes from 'prop-types';
import Stack from '@mui/material/Stack';
import Link from '@mui/material/Link';
import Button from '@mui/material/Button';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Typography from '@mui/material/Typography';

export default function BackButton({ memType }) {
  const to = `/memories/${memType}`;
  if (memType !== 'preview') {
    return (
      <Stack
        direction="row"
        justifyContent="flex-start"
        sx={{ backgroundColor: 'button.main', paddingX: 2, paddingY: 1 }}
      >
        <Link href={to} underline="none">
          <Button
            variant="contained"
            component="label"
            sx={{
              color: 'primary.main',
              backgroundColor: 'button.main',
              padding: '6px 8px',
              boxShadow: 0,
              '&:hover': {
                backgroundColor: 'button.main',
              },
            }}
          >
            <ArrowBackIcon size="medium" sx={{ paddingRight: 1 }} />
            <Typography variant="body1" color="muted.main">Memories</Typography>
          </Button>
        </Link>
      </Stack>
    );
  }
  return null;
}

BackButton.propTypes = {
  memType: PropTypes.string.isRequired,
};
