import PropTypes from 'prop-types';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import Chip from '@mui/material/Chip';
import TableHead from '@mui/material/TableHead';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';

export default function StatusTable({ invitation, accept }) {
  const colorMap = {
    pending: 'warning',
    sent: 'success',
    failed: 'error',
    rejected: 'error',
    accepted: 'success',
  };
  return (
    <Table size="small" aria-label="statuses">
      <TableHead>
        <TableRow>
          <TableCell align="left">Invitation</TableCell>
          <TableCell align="left">Accepted</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        <TableRow key={new Date()}>
          <TableCell align="left">
            <Chip color={colorMap[invitation]} size="small" label={invitation} />
          </TableCell>
          <TableCell align="left">
            <Chip color={colorMap[accept]} size="small" label={accept} />
          </TableCell>
        </TableRow>
      </TableBody>
    </Table>
  );
}

StatusTable.propTypes = {
  invitation: PropTypes.string.isRequired,
  accept: PropTypes.string.isRequired,
};
