import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import SwipeableViews from 'react-swipeable-views';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import CardHeader from '@mui/material/CardHeader';
import { alpha } from '@mui/material/styles';
import { autoPlay, virtualize, bindKeyboard } from 'react-swipeable-views-utils';

const EnhancedSwipeableViews = bindKeyboard(autoPlay(virtualize(SwipeableViews)));

function mod(count, total) {
  const modulus = count % total;
  if (modulus < 0) {
    return modulus + total;
  }
  return modulus;
}

export default function MemoryImageSlide({
  ind, images, handleNext, handleStepChange,
}) {
  const [activeStep, setActiveStep] = useState(ind);
  const [loadedImages, setLoadedImages] = useState(0);
  const [display, setDisplay] = useState(null);

  useEffect(() => {
    setActiveStep(ind);
  }, [ind]);

  useEffect(() => {
    if (loadedImages < images.length) {
      setDisplay('none');
    } else {
      setDisplay('flex');
    }
  }, [loadedImages]);

  const handleImageLoad = () => setLoadedImages((prevLoaded) => prevLoaded + 1);

  const slideRenderer = (props) => {
    /* eslint-disable  react/prop-types */
    const { index, key } = props;
    const virtNum = mod(index, images.length);
    const srcSetList = images[virtNum].images ? (
      images[virtNum].images.map(({ src, width }) => `${src} ${width}w`)
    ) : [];
    const srcSet = srcSetList.join(',');
    const style = {
      display: 'block',
      boxSizing: 'content-box',
      cursor: 'pointer',
      objectFit: 'contain',
      height: '100vh',
    };

    return (
      <Button
        key={key}
        style={style}
        sx={{
          display: 'flex',
          justifyContents: 'center',
          width: '100%',
          bgcolor: '#1c1c1c',
          padding: 0,
        }}
      >
        {images[virtNum].caption ? (
          <CardHeader
            title={images[virtNum].caption}
            sx={{
              position: 'absolute',
              bottom: 40,
              left: '50%',
              bgcolor: alpha('#1c1c1c', 0.4),
              borderRadius: 2,
              padding: 1,
              color: 'button.main',
              transform: 'translate(-50%, 0)',
            }}
          >
            {images[virtNum].caption}
          </CardHeader>
        ) : null}
        <Box
          component="img"
          onLoad={handleImageLoad}
          sx={{
            display: { display },
            justifyContents: 'center',
            width: '100%',
            bgcolor: '#1c1c1c',
          }}
          src={images[virtNum].src}
          srcSet={srcSet}
          style={style}
          alt={images[virtNum].src}
          key={key}
        />

      </Button>
    );
  };

  return (
    <EnhancedSwipeableViews
      index={activeStep}
      onChangeIndex={handleStepChange}
      enableMouseEvents
      animateTransitions
      interval={10000}
      onClick={handleNext}
      style={{ backgroundColor: 'muted.main' }}
      slideRenderer={slideRenderer}
    />
  );
}

MemoryImageSlide.defaultProps = {
  ind: 0,
};

MemoryImageSlide.propTypes = {
  ind: PropTypes.number,
  handleNext: PropTypes.func.isRequired,
  handleStepChange: PropTypes.func.isRequired,
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
};
