import PropTypes from 'prop-types';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import { alpha } from '@mui/material/styles';
import Typography from '@mui/material/Typography';

export default function ThumbnailChooser({ index, thumbnail, handleThumbnailChange }) {
  return (
    <FormControlLabel
      control={<Radio sx={{ padding: 0 }} />}
      label={(thumbnail === index) ? <Typography sx={{ fontWeight: 600, overflow: 'hidden', fontSize: '0.8rem' }}>Thumbnail</Typography> : ''}
      checked={thumbnail === index}
      onChange={() => handleThumbnailChange(index)}
      sx={{
        pr: 1,
        position: 'absolute',
        bgcolor: alpha('#fff', 0.7),
        color: alpha('#000', 0.7),
        borderRadius: '0px 5px 5px 0px',
        borderColor: 'secondary.main',
        borderLeft: 0,
        bottom: 5,
        left: 2,
        margin: 0,
      }}
    />
  );
}

ThumbnailChooser.propTypes = {
  index: PropTypes.number.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
};
