import { useState } from 'react';
import commonUtils from 'utils';

function ownEmail() {
  const cookieValue = commonUtils.getUserInfo();
  return cookieValue.email;
}

export default function useEmail() {
  const [items, setItems] = useState([]);
  const [value, setValue] = useState('');
  const [error, setError] = useState(null);

  const isInList = (email) => items.includes(email);

  // eslint-disable-next-line
  const isEmail = (email) => /[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/.test(email);

  const isValid = (email) => {
    let err = null;

    if (isInList(email)) {
      err = `${email} has already been added.`;
    }

    if (!isEmail(email)) {
      err = `${email} is not a valid email address.`;
    }

    if (email === ownEmail()) {
      err = 'Cannot share memory with owner email.';
    }

    if (err) {
      setError(err);
      return false;
    }

    return true;
  };

  const handleKeyDown = (evt) => {
    if (['Enter', 'Tab', ','].includes(evt.key)) {
      evt.preventDefault();

      const val = value.trim();

      if (val && isValid(val)) {
        setItems([...items, val]);
        setValue('');
      }
    }
  };

  const handleChange = (evt) => {
    setValue(evt.target.value.trim());
    setError(null);
  };

  const handleDelete = (item) => {
    setItems((prevItems) => prevItems.filter((i) => i !== item));
  };

  const handlePaste = (evt) => {
    evt.preventDefault();

    const paste = evt.clipboardData.getData('text');
    // eslint-disable-next-line
    var emails = paste.match(/[\w\d\.-]+@[\w\d\.-]+\.[\w\d\.-]+/g);

    if (emails) {
      const toBeAdded = emails.filter((email) => !isInList(email.trim()));
      setItems([...items, ...toBeAdded]);
    }
  };

  return [items, value, error, setItems, handleKeyDown, handleChange, handleDelete, handlePaste];
}
