import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import Dialog from '@mui/material/Dialog';
import Divider from '@mui/material/Divider';
import DoneIcon from '@mui/icons-material/Done';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

import './memory.css';
import SimpleSnackbar from 'components/simple_snackbar';
import BackButton from './BackButton';
import MemoryCarousel from './MemoryCarousel';
import MemoryContentStructure from './ContentStructure';
import MemoryDate from './MemoryDate';
import MemoryImages from './MemoryImages';
import MemoryStory from './MemoryStory';
import MemoryTitle from './MemoryTitle';
import api from '../Api';

export default function MemoryContent({
  memType,
  memTitle,
  memDate,
  memStory,
  memImages,
  memThumbnail,
  bucket,
  memoryID,
  authURL,
}) {
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [title, setTitle] = useState(memTitle);
  const [date, setDate] = useState(memDate);
  const [story, setStory] = useState(memStory);
  const [thumbnail, setThumbnail] = useState(memThumbnail);
  const [images, setImages] = useState(memImages);
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);

  const [newImages, setNewImages] = useState([]);
  const [newImagesCount, setNewImagesCount] = useState(null);
  const [errorUploadImages, setErrorUploadImages] = useState([]);
  const [addImageOpen, setAddImageOpen] = useState(false);
  const [uploadImageProgress, setUploadImageProgress] = useState(null);
  const [uploadImageStatus, setUploadImageStatus] = useState(null);
  const [snackError, setSnackError] = useState(null);
  const [httpError, setHTTPError] = useState(false);

  const maxSteps = images.length;

  useEffect(() => setTitle(memTitle), [memTitle]);
  useEffect(() => setDate(memDate), [memDate]);
  useEffect(() => setStory(memStory), [memStory]);
  useEffect(() => setImages(memImages), [memImages]);
  useEffect(() => setThumbnail(memThumbnail), [memThumbnail]);

  const handleSnackMessage = (msg, error) => {
    setSnackMessage(msg);
    setSnackError(error);
  };

  const handleHTTPError = (response, operation, callback) => {
    if (response.status === 401) {
      handleSnackMessage('You have been logged out. Please login to continue.', true);
    } else if (response.status === 403) {
      handleSnackMessage('You do not have permissions to do this operation.', true);
    } else {
      handleSnackMessage(`Unknown error occured in ${operation}`, true);
    }
    callback(response.status);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const httpErrorCallback = (status) => {
    setSnack(true);
    setHTTPError(status);
  };

  const handleResponse = (response, message, value, callback) => {
    if (response.ok) {
      handleSnackMessage(`${message} successful`, false);
      setSnack(true);
      callback(value);
    } else {
      handleHTTPError(response, message, httpErrorCallback);
    }
  };

  const updateTitle = async (value) => {
    const resp = await api.updateTitle(memoryID, value);
    handleResponse(resp, 'title update', value, setTitle);
  };

  const updateDate = async (value) => {
    const resp = await api.updateDate(memoryID, value);
    handleResponse(resp, 'date update', value, setDate);
  };

  const updateStory = async (value) => {
    const resp = await api.updateStory(memoryID, value);
    handleResponse(resp, 'story update', value, setStory);
  };

  const updateThumbnail = async (index) => {
    const resp = await api.updateThumbnail(memoryID, images[index].title);
    handleResponse(resp, 'thumbnail update', index, setThumbnail);
  };

  const updateCaption = async (index, value) => {
    const resp = await api.updateCaption(memoryID, images[index].title, value);
    handleResponse(resp, 'caption update', value, () => {});

    setImages([
      ...images.slice(0, index),
      {
        ...images[index],
        caption: value,
      },
      ...images.slice(index + 1),
    ]);
  };

  const deleteImage = async (index) => {
    try {
      let resp;
      const value = images[index].title;
      if (images.length === 1) {
        handleSnackMessage('Atleast 1 image is needed for memory. Unable to delete', true);
      } else {
        resp = await api.removeImage(memoryID, value);
        handleResponse(
          resp,
          'image delete',
          value,
          () => setImages(images.filter((item) => item.title !== value)),
        );
        if (index <= thumbnail && thumbnail > 0) {
          setThumbnail(thumbnail - 1);
        }
      }
      setSnack(true);
      return resp;
    } catch (error) {
      /* eslint-disable no-console */
      console.log(error);
      return false;
    }
  };

  const addImage = async (details) => {
    try {
      const resp = await api.addImage(memoryID, details);
      return resp;
    } catch (error) {
      /* eslint-disable no-console */
      console.log(error);
      return false;
    }
  };

  const handleNewImages = async () => {
    /* eslint-disable  no-await-in-loop, no-restricted-syntax */
    for (const [index, img] of newImages.entries()) {
      const [det, file, src] = img;
      const putURL = await addImage(det).then((response) => {
        if (response.ok) {
          return response.text();
        }
        handleHTTPError(response, 'add image', httpErrorCallback);
        return '';
      });
      const photo = {
        key: `memories/${memoryID}/images/${det.name}`,
        src,
        alt: det.name,
        title: det.name,
        width: det.width,
        height: det.height,
        images: [],
      };
      const uploadResp = await fetch(putURL, { body: file, method: 'PUT' });
      if (uploadResp.ok) {
        setUploadImageProgress(`Images uploaded: ${index + 1} / ${newImagesCount} `);
        setImages((pics) => pics.concat([photo]));
      } else {
        setUploadImageStatus('Error while uploading memory image');
        setErrorUploadImages((errImgs) => errImgs.concat([det.name]));
        handleHTTPError(uploadResp, 'deleting image', httpErrorCallback);
      }
    }
  };

  useEffect(async () => {
    if (newImages.length > 0) {
      setAddImageOpen(true);
      setUploadImageProgress('Preparing images for upload....');
    }
    if (newImages.length === newImagesCount) {
      await handleNewImages();
      if (errorUploadImages.length > 0) {
        setUploadImageStatus(`Error while uploading ${errorUploadImages.length} images`);
      } else {
        setUploadImageStatus(`${newImages.length} image(s) uploaded successfully`);
      }
      setErrorUploadImages([]);
      setNewImagesCount(null);
      setNewImages([]);
      setTimeout(() => {
        setAddImageOpen(false);
        setUploadImageStatus(null);
        setUploadImageProgress(null);
      }, 3000);
    }
  }, [newImages]);

  const handleImageAdd = async (event) => {
    if (event.target.files) {
      const files = Array.from(event.target.files);
      setNewImagesCount(files.length);
      if (images.length + files.length <= 15) {
        files.forEach((file) => {
          const image = new Image();
          image.onload = async (e) => {
            const height = e.target.naturalHeight;
            const width = e.target.naturalWidth;
            const size = file.size / (1024 * 1024);
            const { name } = file;
            const resImages = [500, 1000, 1500].map((resHeight) => ({
              height: resHeight, width: Math.ceil((resHeight * width) / height),
            }));
            const creationDate = Date.now();
            const newName = `${creationDate}-${name}`;
            const imageDetails = [{
              name: newName, size, height, width, images: resImages,
            }, file, e.target.src];
            setNewImages((pics) => pics.concat([imageDetails]));
          };
          image.src = URL.createObjectURL(file);
        });
        files.map((file) => URL.revokeObjectURL(file)); // avoid memory leak
      } else {
        handleSnackMessage(`Unable to add ${files.length} images. Maximum images allowed per memory is 15`, true);
        setSnack(true);
      }
    }
  };

  const handleClose = () => setOpen(false);

  const handleActiveIndexChange = (step) => setActiveIndex(step);

  const handleBack = () => {
    setActiveIndex(
      (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps,
    );
  };

  const handleNext = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handlePhotoClick = (photo, event, index) => {
    setOpen(true);
    setActiveIndex(index);
  };

  const handleEditClick = () => setEdit(true);

  const memoryTitle = (
    <MemoryTitle
      editable={edit}
      text={title}
      handleTitleUpdate={updateTitle}
    />
  );

  const memoryDate = (
    <MemoryDate editable={edit} prevDate={date} handleDateUpdate={updateDate} />
  );

  const storyLayout = (
    <MemoryStory
      editable={edit}
      prevStory={story}
      handleStoryUpdate={updateStory}
    />
  );

  const photoLayout = (
    <MemoryImages
      editable={edit}
      images={images}
      memThumbnail={thumbnail}
      handlePhotoClick={handlePhotoClick}
      handleThumbnailUpdate={updateThumbnail}
      deleteImage={deleteImage}
      addImage={handleImageAdd}
      updateCaption={updateCaption}
    />
  );

  const updateDone = (
    <Button
      variant="contained"
      component="label"
      onClick={() => setEdit(false)}
      sx={{
        color: 'primary.main',
        borderRadius: 5,
        backgroundColor: 'button.main',
        padding: '6px 8px',
      }}
    >
      <DoneIcon size="medium" sx={{ paddingRight: 1 }} />
      <Typography variant="body1" color="muted.main">Edit</Typography>
    </Button>
  );

  if (httpError === 401 && snack === false) {
    return <Navigate to="/login" />;
  }

  return (
    <>
      {edit ? (
        <Stack
          direction="row"
          justifyContent="flex-start"
          sx={{ backgroundColor: 'button.main', paddingX: 2, paddingY: 1 }}
        >
          {updateDone}
        </Stack>
      ) : <BackButton memType={memType} /> }
      <Box
        component="div"
        sx={{
          backgroundColor: 'button.main',
          margin: edit ? '5px' : 0,
        }}
      >
        <MemoryContentStructure
          header={memoryTitle}
          subheader={memoryDate}
          photoLayout={photoLayout}
          storyLayout={storyLayout}
          memoryID={memoryID}
          bucket={bucket}
          authURL={authURL}
          handleEditClick={handleEditClick}
          memType={memType}
        />
      </Box>
      <MemoryCarousel
        open={open}
        title={title}
        date={date}
        images={images}
        index={activeIndex}
        handleNext={handleNext}
        handleBack={handleBack}
        handleClose={handleClose}
        handleChange={handleActiveIndexChange}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
        error={snackError}
      />
      <Dialog
        open={addImageOpen}
        fullWidth
        sx={{
          p: 1,
        }}
      >
        <Card sx={{ maxWidth: 1, p: 1 }}>
          <CardHeader title="Adding Images" sx={{ textAlign: 'center' }} />
          <Divider />
          <CardContent>
            <Typography variant="h6" sx={{ color: 'muted.main' }}>{uploadImageProgress}</Typography>
            <Stack justifyContent="center" sx={{ py: 2 }}>
              <Typography variant="h5">{uploadImageStatus}</Typography>
            </Stack>
          </CardContent>
        </Card>
      </Dialog>

    </>
  );
}

MemoryContent.defaultProps = {
  memTitle: 'Memory Title',
  memDate: 'Memory Date',
  memStory: 'Memory Text',
};

MemoryContent.propTypes = {
  memType: PropTypes.string.isRequired,
  memTitle: PropTypes.string,
  memDate: PropTypes.string,
  memStory: PropTypes.string,
  memoryID: PropTypes.string.isRequired,
  memThumbnail: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
  memImages: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
};
